.castList {
  display: flex;
  flex-direction: column;
  gap: 30px;
  padding-top: 30px;
  padding-left: 10px;
}

.castItem {
  width: 200px;
  position: relative;
  list-style-type: none;
}

.castImg {
  width: 200px;
  height: 300px;
  background-color: rgba(0 0 0 / 0.2);
  border-radius: var(--border-radius);
  margin-bottom: 10px;
}

.noPhoto {
  font-size: 20px;
  font-weight: var(--font-weight);
  padding-left: 40px;
  margin-bottom: 15px;
}

.castName {
  font-size: 18px;
  padding-left: 4px;
  margin-bottom: 4px;
}

.castCharacter {
  font-size: 18px;
  padding-left: 4px;
}

.noCast {
  font-size: 20px;
  font-weight: var(--font-weight);
  padding-top: 20px;
  padding-left: 10px;
}
